.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transition: all 0.2s ease;
  background: transparent;
  backdrop-filter: blur(8px);
  padding: 0.75rem 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
  height: 64px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.navbar.scrolled {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(12px);
}

.navbar-container {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-brand {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.navbar-brand span:last-child {
  color: #fff;
  font-size: 1.25rem;
  font-weight: 500;
}

.navbar-brand .material-icons-round {
  color: #6366f1;
  font-size: 1.75rem;
}

.navbar-buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.navbar-buttons button {
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  transition: opacity 0.2s ease;
  cursor: pointer;
}

.login-btn {
  background: transparent;
  color: rgba(255, 255, 255, 0.85);
  border: none;
  padding: 0.5rem 0.75rem;
}

.login-btn:hover {
  opacity: 0.9;
}

.signup-btn {
  background: #6366f1;
  color: white;
  border: none;
}

.signup-btn:hover {
  opacity: 0.9;
}

@media (max-width: 768px) {
  .navbar-container {
    padding: 0 1rem;
  }

  .navbar-buttons {
    gap: 0.75rem;
  }
}
