.logo-section {
  text-align: center;
  width: 100%;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}

.logo-section h2 {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 1rem 0 2rem;
  background: linear-gradient(135deg, #fff 0%, #a5b4fc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 2;
}

.logo-wall-container {
  flex: 1;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.logo-wall {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.logo-wall.horizontal .logo-track {
  display: flex;
  align-items: center;
  white-space: nowrap;
}

.logo-wall.horizontal .logo-track-inner {
  display: inline-flex;
  align-items: center;
  gap: 4rem;
  padding-right: 4rem;
  animation: scroll var(--duration) linear infinite;
}

.logo-item {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--logo-size);
  aspect-ratio: 3/2;
  padding: 1.5rem;
}

.logo-item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: grayscale(100%) brightness(0.8) contrast(0.8);
  opacity: 0.4;
  transition: all 0.3s ease;
}

.logo-item:hover img {
  filter: grayscale(0%) brightness(1) contrast(1);
  opacity: 1;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-100%));
  }
}
