.pricing {
  background: #0a0118;
  padding: 6rem 2rem;
  position: relative;
  overflow: hidden;
}

.pricing-container {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.pricing-header {
  text-align: center;
  margin-bottom: 4rem;
}

.pricing-header span {
  color: var(--primary-color);
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.pricing-header h2 {
  font-size: 3.5rem;
  font-weight: 700;
  margin: 1rem 0;
  background: linear-gradient(135deg, #fff 0%, #a5b4fc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.pricing-header p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto;
}

.pricing-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 2rem 0 4rem;
}

.toggle-button {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 0.5rem 1.5rem;
  border-radius: 2rem;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-button.active {
  background: rgba(165, 180, 252, 0.1);
  border-color: var(--primary-color);
  color: #fff;
}

.discount-badge {
  background: var(--primary-color);
  color: #fff;
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.875rem;
  margin-left: 0.5rem;
}

.pricing-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  padding: 1rem;
}

.pricing-card {
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 1rem;
  padding: 2rem;
  transition: all 0.3s ease;
  position: relative;
  backdrop-filter: blur(10px);
}

.pricing-card.popular {
  background: rgba(165, 180, 252, 0.05);
  border-color: rgba(165, 180, 252, 0.2);
  transform: translateY(-0.5rem);
}

.popular-badge {
  position: absolute;
  top: -0.75rem;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--secondary-color) 100%);
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  font-size: 0.875rem;
  white-space: nowrap;
}

.pricing-card:hover {
  border-color: rgba(165, 180, 252, 0.3);
  transform: translateY(-0.25rem);
}

.pricing-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #fff;
}

.pricing-card .subtitle {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.875rem;
  margin-bottom: 2rem;
}

.price {
  font-size: 2.5rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 0.5rem;
  position: relative;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.price.animating {
  animation: priceChange 0.3s ease;
}

@keyframes priceChange {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  50% {
    opacity: 0;
    transform: translateY(-10px);
  }
  51% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.price span {
  font-size: 1rem;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.5);
}

.pricing-card button {
  width: 100%;
  padding: 0.75rem;
  border-radius: 0.5rem;
  border: none;
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  font-weight: 500;
  margin: 2rem 0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.pricing-card.popular button {
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--secondary-color) 100%);
}

.pricing-card button:hover {
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--secondary-color) 100%);
  transform: translateY(-2px);
}

.features-list {
  margin-top: 2rem;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
  color: rgba(255, 255, 255, 0.7);
}

.feature-item i {
  color: var(--primary-color);
  font-size: 1.25rem;
}

@media (max-width: 1200px) {
  .pricing-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .pricing {
    padding: 4rem 1rem;
  }

  .pricing-header h2 {
    font-size: 2.5rem;
  }

  .pricing-grid {
    grid-template-columns: 1fr;
  }

  .pricing-card.popular {
    transform: none;
  }
}
