.spotlight-card {
  position: relative;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 20px;
  padding: 2rem;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
}

.spotlight-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
    800px circle at var(--spotlight-x) var(--spotlight-y),
    var(--spotlight-color),
    transparent 40%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
}

.spotlight-card.hovered::before {
  opacity: 1;
}

.spotlight-content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
}

.spotlight-card i {
  font-size: 2rem;
  color: #fff;
  margin-bottom: 1rem;
}

.spotlight-card h2 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #fff;
  margin: 0;
}

.spotlight-card p {
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.6;
  margin: 0;
}

.spotlight-card button {
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-weight: 500;
  transition: all 0.3s ease;
}

.spotlight-card button:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}
