.dashboard-section {
  padding: 6rem 2rem;
  background: #0a0118;
  position: relative;
  overflow: hidden;
}

.dashboard-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(99, 102, 241, 0.3), transparent);
}

.dashboard-content {
  max-width: 1400px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 0.8fr 1.4fr;
  gap: 4rem;
  align-items: center;
}

.dashboard-text {
  padding-right: 2rem;
}

.dashboard-text h2 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #fff 0%, #a5b4fc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.dashboard-text > p {
  font-size: 1.2rem;
  color: #94a3b8;
  margin-bottom: 3rem;
  line-height: 1.6;
}

.dashboard-features {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.dashboard-features li {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  padding: 1.5rem;
  background: rgba(99, 102, 241, 0.03);
  border: 1px solid rgba(99, 102, 241, 0.1);
  border-radius: 16px;
  transition: all 0.3s ease;
}

.dashboard-features li:hover {
  background: rgba(99, 102, 241, 0.05);
  border-color: rgba(99, 102, 241, 0.2);
  transform: translateY(-2px);
}

.dashboard-features .material-icons-round {
  font-size: 1.5rem;
  color: #6366f1;
  background: rgba(99, 102, 241, 0.1);
  padding: 0.8rem;
  border-radius: 12px;
  flex-shrink: 0;
}

.dashboard-features h3 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #fff;
  margin-bottom: 0.5rem;
}

.dashboard-features p {
  color: #94a3b8;
  line-height: 1.5;
}

.dashboard-image {
  position: relative;
  margin-right: -4rem;
}

.dashboard-image img {
  width: 120%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.5);
}

.dashboard-image::before {
  content: '';
  position: absolute;
  inset: -10px;
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.1), transparent);
  border-radius: 20px;
  z-index: -1;
}

@media (max-width: 1200px) {
  .dashboard-image {
    margin-right: 0;
  }
  
  .dashboard-image img {
    width: 100%;
  }
}

@media (max-width: 1024px) {
  .dashboard-content {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .dashboard-text {
    padding-right: 0;
  }

  .dashboard-features {
    align-items: center;
  }

  .dashboard-features li {
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 400px;
  }
  
  .dashboard-image {
    margin: 2rem auto 0;
    max-width: 900px;
  }
}

@media (max-width: 768px) {
  .dashboard-section {
    padding: 4rem 1rem;
  }

  .dashboard-text h2 {
    font-size: 2.5rem;
  }
}
